.profile__edit {
  margin: 0 30px 0 0;
  padding: 0;
  border-radius: 50%;
  background-color: inherit;
  border: none;
  max-width: 120px;
  max-height: 120px;
  display: grid;
}
@media screen and (max-width: 707px) {
  .profile__edit {
    align-self: center;
    margin: 0 0 26px 0;
  }
}
