.profile__info {
  grid-column: 2/3;
  display: grid;
  max-width: 580px;
  grid-template-columns: max-content min-content;
}

@media screen and (max-width: 707px) {
  .profile__info {
    grid-template-columns: 28px max-content min-content;
    align-items: center;
    flex-wrap: wrap;
  }
}
